.wrapper h3,
.wrapper p,
.wrapper h2 {
  color: black !important;
  margin: 0;
  padding: 0;
}
.wrapper h2 {
  font-size: 35px;
}

.Slide {
  display: flex;
  justify-content: space-between;
}

.left {
  display: flex;
  align-items: center;
  gap: 30px;
}

.wrapper img {
  box-shadow: 0px 0px 10px #ed254d62;
}

.texts {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 10px;
}

.Price {
  font-weight: 500;
  font-size: 20px;
}

.buttos {
  display: flex;
  align-items: flex-start;
  gap: 10px;
}

.buttos button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  font-size: 30px;
}

.dailyBox {
  padding: 30px;
  border-radius: 20px;
  box-shadow: 0px 0px 10px #ed254d62;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.info {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.settings {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 20px;
}
.settings button {
  background-color: transparent;
  border: none;
  display: flex;
  align-items: center;
  gap: 10px;
}

.settings button i {
  background-color: #ed254d;
  border: 1px solid #ed254d;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  font-size: 30px;
  color: white;
}
